.circle{
    overflow: hidden;
    position: relative;
}
.left-wrap{
    overflow: hidden;
    position: absolute;
    top:0;
}
.right-wrap {
    overflow: hidden;
    position: absolute;
    top:0;
}

.loader{
    position:absolute;
    left:0;
    top:0;
    border-radius:1000;
    transform-origin: 0 50%;
}
.loader2{
    position:absolute;
    left:0;
    top:0;
    border-radius:1000;
    transform-origin: 100% 50%;
}

.inner-circle {
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.circle .text {
    font-size: 11;
    color: #888;
}