body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


select.form-control,
textarea.form-control,
input.form-control {
  font-size: 16px;
}
input[type=file] {
  width: 100%;
}
.App {
    margin-top: 1.5em;
}

.navbar-dark .navbar-brand {
    color: #fec503;
}

.navbar-dark .nav-item {
    text-transform: uppercase;
    font-family: Montserrat, "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.navbar-dark .nav-item .nav-link, .navbar-dark .nav-item, .navbar-dark .navbar-brand:focus{
    color: #fec503;
}


.navbar-brand {
    text-transform: uppercase;
    font-family: Montserrat, "Helvetica Neue", Helvetica, Arial, sans-serif;
    /*font-size: 20px;*/
    font-weight: 400;
    letter-spacing: 1px;
}


.Home .lander {
  padding: 80px 0;
  text-align: center;
}

.Home .lander h1 {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
}

.Home .lander p {
  color: #999;
}

.Home .media-list a {
  color: black;
  text-decoration: none;
}

.Home .media-list .hoverable:hover {
  background-color: #f8f9fa;
}


.circle{
    overflow: hidden;
    position: relative;
}
.left-wrap{
    overflow: hidden;
    position: absolute;
    top:0;
}
.right-wrap {
    overflow: hidden;
    position: absolute;
    top:0;
}

.loader{
    position:absolute;
    left:0;
    top:0;
    border-radius:1000;
    -webkit-transform-origin: 0 50%;
            transform-origin: 0 50%;
}
.loader2{
    position:absolute;
    left:0;
    top:0;
    border-radius:1000;
    -webkit-transform-origin: 100% 50%;
            transform-origin: 100% 50%;
}

.inner-circle {
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.circle .text {
    font-size: 11;
    color: #888;
}
@media all and (min-width: 480px) {

  .Login form {
    margin: 0 auto;
    max-width: 320px;
  }
}
.LoaderButton .spinning {
  margin-right: 7px;
  top: 2px;
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}
@-webkit-keyframes spin {
  from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
  to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
}
@keyframes spin {
  from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
  to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
}
@media all and (min-width: 480px) {


  .Signup form {
    margin: 0 auto;
    max-width: 320px;
  }
}

.Signup form span.help-block {
  font-size: 14px;
  padding-bottom: 10px;
  color: #999;
}
.Paragraphs {
  top: 2px;
}

@media all and (min-width: 480px) {

  .ResetPassword form {
    margin: 0 auto;
    max-width: 320px;
  }

  .ResetPassword .success {
    max-width: 400px;
  }
}

.ResetPassword .success {
  margin: 0 auto;
  text-align: center;
}
.ResetPassword .success .glyphicon {
  color: grey;
  font-size: 30px;
  margin-bottom: 30px;
}
.NotFound {
  padding-top: 100px;
  text-align: center;
}
